import CoinStoreInstance from '../../domains/stores/CoinStore';
import CustomElement from '../../abstracts/CustomElement';
import { $ } from '../../utils/dom';

class CoinCurrentState extends CustomElement {
  connectedCallback() {
    super.connectedCallback();
    CoinStoreInstance.subscribe(this);
  }

  template() {
    return `
      <p>  : <span class="money">0</span></p>
      <h2>  </h2>
      <table class="coin-current-state">
        <thead>
          <tr>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>500</td>
            <td class="coin-500-count-td">0</td>
          </tr>
          <tr>
            <td>100</td>
            <td class="coin-100-count-td">0</td>
          </tr>
          </tr>
          <tr>
            <td>50</td>
            <td class="coin-50-count-td">0</td>
          </tr>
          <tr>
            <td>10</td>
            <td class="coin-10-count-td">0</td>
          </tr>
        </tbody>
      </table>
    `;
  }

  rerender(newCoinsCount) {
    $('.money').textContent = newCoinsCount.sum;
    $('.coin-500-count-td').textContent = `${newCoinsCount[500]}`;
    $('.coin-100-count-td').textContent = `${newCoinsCount[100]}`;
    $('.coin-50-count-td').textContent = `${newCoinsCount[50]}`;
    $('.coin-10-count-td').textContent = `${newCoinsCount[10]}`;
  }
}

customElements.define('coin-current-state', CoinCurrentState);

export default CoinCurrentState;
